import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import axios from 'axios';
import {img_500 , unavailable , unavailableLandscape} from './config.js'
import { Button } from "@material-ui/core";
import { useState,useEffect } from 'react'
import YouTubeIcon from '@material-ui/icons/YouTube';
import './ContentModal.css'

const useStyles = makeStyles((theme) => ({
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        width: "90%",
        height: "80%",
        backgroundColor: "#39445a",
        border: "1px solid #282c34",
        borderRadius: 10,
        color: "white",
        boxShadow: theme.shadows[5],
        padding: theme.spacing(1, 1, 3),
    },
}));

export default function ContentModal( {children , type , id} ) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [content,setContent] = useState();
  const [video,setVideo] = useState();

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const fetchData = async ()=>{
      const {data} = await axios.get(`https://api.themoviedb.org/3/${type}/${id}?api_key=f2d277071b3575e2362951ad0c7a09bf&language=en-US`)
      console.log(data);
      setContent(data);
  }

  const fetchVideos = async ()=>{
    const {data} = await axios.get(`https://api.themoviedb.org/3/${type}/${id}/videos?api_key=f2d277071b3575e2362951ad0c7a09bf&language=en-US`)
    console.log(data);
    setVideo(data.results[0]?.key);
}

useEffect(() => {
    fetchData();
    fetchVideos();
},[])


  return (
    <div>
      <div onClick={fetchData} className="media" onClick={handleOpen}>
        {children}
      </div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          {content && (
                <div className={classes.paper}>
                    <div className="ContentModal">
                        <img className="ContentModal__portrait" src={content.poster_path?`${img_500}/${content.poster_path}`:unavailable} alt = {content.title || content.name}></img>
                        <img className="ContentModal__landscape" src={content.backdrop_path?`${img_500}/${content.backdrop_path}`:unavailableLandscape} alt = {content.title || content.name}></img>
                    <div className="ContentModal__about">
                        <span className="ContentModal__title">{content.title || content.name} ({(content.release_date || content.first_air_date || "----").substring(0,4)})</span>
                        {content.tagline && (
                            <i className="tagline">{content.tagline}</i>
                        )}
                        <span className="ContentModal__description">{content.overview}</span>
                        <Button
                        style = {{width: '100%', marginTop:'20px'}}
                        variant = "contained"
                        startIcon = {<YouTubeIcon/>}
                        color = "secondary"
                        target="_blank"
                        href = {`https://www.youtube.com/watch?v=${video}`}
                        >
                            Watch the trailer.
                        </Button>
                    </div>
                    </div>
                </div>
            )}
        </Fade>
      </Modal>
    </div>
  );
}